import { Box, Typography, ThemeProvider, Button, Stack } from '@mui/material';
import { useState } from 'react';
import { lightTheme } from './components/Theme';
import { questions } from './helpers/questions';

interface Question { question: string; tags: string[]; }

function App() {
  const [page, setPage] = useState<'HOME' | 'QUESTIONS' | 'RESULT'>('HOME');
  const [couponType, setCouponType] = useState<'ZACHCIANKA' | 'DANIE'>('ZACHCIANKA');
  const [selectedCategory, setSelectedCategory] = useState<string | null>(null);
  const [selectedQuestions, setSelectedQuestions] = useState<Question[]>([]);
  const [questionIndex, setQuestionIndex] = useState<number>(0);
  const [selectedTags, setSelectedTags] = useState<string[]>([]);

  const handleCategorySelection = (category: string) => {
    setSelectedCategory(category);
    let questionsPool: Question[] = [];
    if (category === 'skadMamWiedziec') {
      questionsPool = [...questions.jedzonko, ...questions.rozrywka, ...questions.relaksik];
    } else if (category === 'jedzonko') questionsPool = questions.jedzonko;
    else if (category === 'rozrywka') questionsPool = questions.rozrywka;
    else if (category === 'relaksik') questionsPool = questions.relaksik;
    const shuffled = questionsPool.sort(() => Math.random() - 0.5);
    setSelectedQuestions(shuffled.slice(0, 3));
    setQuestionIndex(0);
  };

  const handleAnswer = (answer: boolean) => {
    const currentQuestion = selectedQuestions[questionIndex];
    if (answer) setSelectedTags((prev) => [...prev, ...currentQuestion.tags]);
    questionIndex < 2 ? setQuestionIndex(questionIndex + 1) : setPage('RESULT');
  };

  return (
    <ThemeProvider theme={lightTheme}>
      <Box display="flex" justifyContent="center" minHeight="100vh" bgcolor="#E5E4D7">
        <Stack display="flex" flexDirection="column" justifyContent="start" alignItems="center" margin="2rem 1rem" spacing={2}>
          <Typography variant="h2" color="secondary" textAlign="center" fontFamily="vintages">KUPONIK</Typography>
          {page === 'HOME' && (
            <>
              <img src={process.env.PUBLIC_URL + "/pics/bubu_thinking.png"} style={{ height: '20rem' }} alt="Bubu myli" />
              <Typography variant="h5" color="secondary" textAlign="center" fontFamily="nyala">Mam dzi ochot na...</Typography>
              <Stack spacing={2}>
                <Button variant="contained" color="primary" sx={{ fontSize: "2rem", fontFamily: "nyala" }} size="large" onClick={() => { setPage('QUESTIONS'); setCouponType('ZACHCIANKA'); }}>
                  Zachcianke <img src={process.env.PUBLIC_URL + "/pics/bubu_want.png"} style={{ height: '3rem', marginLeft: '2px' }} alt="Chce" />
                </Button>
                <Button variant="contained" color="primary" sx={{ fontSize: "2rem", fontFamily: "nyala" }} size="large" onClick={() => { setPage('QUESTIONS'); setCouponType('DANIE'); }}>
                  danie <img src={process.env.PUBLIC_URL + "/pics/bubu_demand.png"} style={{ height: '3rem', marginLeft: '2px' }} alt="da" />
                </Button>
              </Stack>
            </>
          )}
          {page === 'QUESTIONS' && (
            <>
              {!selectedCategory ? (
                <>
                  <Typography variant="h5" color="secondary" textAlign="center" fontFamily="nyala">A konkretniej na...</Typography>
                  <Stack spacing={4} marginTop="2rem">
                    <Button variant="contained" color="primary" sx={{ fontSize: "2rem", fontFamily: "nyala" }} size="large" onClick={() => handleCategorySelection('jedzonko')}>
                      Jedzonko <img src={process.env.PUBLIC_URL + "/pics/bubu_eat.png"} style={{ height: '3.5rem', marginLeft: '2px' }} alt="Jedzenie" />
                    </Button>
                    <Button variant="contained" color="primary" sx={{ fontSize: "2rem", fontFamily: "nyala" }} size="large" onClick={() => handleCategorySelection('rozrywka')}>
                      Rozrywk <img src={process.env.PUBLIC_URL + "/pics/bubu_fun.png"} style={{ height: '3.5rem', marginLeft: '2px' }} alt="Rozrywka" />
                    </Button>
                    <Button variant="contained" color="primary" sx={{ fontSize: "2rem", fontFamily: "nyala" }} size="large" onClick={() => handleCategorySelection('relaksik')}>
                      Relaksik <img src={process.env.PUBLIC_URL + "/pics/bubu_relax.png"} style={{ height: '3.5rem', marginLeft: '2px' }} alt="Relaks" />
                    </Button>
                    <Button variant="contained" color="primary" sx={{ fontSize: "2rem", fontFamily: "nyala" }} size="large" onClick={() => handleCategorySelection('skadMamWiedziec')}>
                      Skd mam wiedzie?! <img src={process.env.PUBLIC_URL + "/pics/bubu_angry.png"} style={{ height: '3.5rem', marginLeft: '2px' }} alt="Nie wiem" />
                    </Button>
                  </Stack>
                </>
              ) : (
                <>
                  <Typography variant="h5" color="secondary" textAlign="center" fontFamily="nyala">{selectedQuestions[questionIndex].question}</Typography>
                  <Stack spacing={2} marginTop="2rem" direction="row">
                    <Button variant="contained" color="primary" sx={{ fontSize: "2rem", fontFamily: "nyala" }} size="large" onClick={() => handleAnswer(true)}>Tak</Button>
                    <Button variant="contained" color="primary" sx={{ fontSize: "2rem", fontFamily: "nyala" }} size="large" onClick={() => handleAnswer(false)}>Nie</Button>
                  </Stack>
                  <Typography variant="caption" color="secondary" textAlign="center" fontFamily="nyala">Pytanie {questionIndex + 1} z 3</Typography>
                </>
              )}
            </>
          )}
          {page === 'RESULT' && (
            <>
              <Typography variant="h4" color="secondary" textAlign="center" fontFamily="nyala">Wynik kuponu:</Typography>
              <Typography variant="body1" color="secondary" textAlign="center" fontFamily="nyala">Wybrane tagi: {selectedTags.join(', ') || 'brak'}</Typography>
              <Button variant="contained" color="primary" sx={{ fontSize: "2rem", fontFamily: "nyala" }} onClick={() => { setPage('HOME'); setSelectedCategory(null); setSelectedQuestions([]); setQuestionIndex(0); setSelectedTags([]); }}>
                Zacznij od nowa
              </Button>
            </>
          )}
        </Stack>
      </Box>
    </ThemeProvider>
  );
}

export default App;
